.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 1rem;
  border-radius: 5px;
  background: #fef3c7;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
  overflow: visible;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: #d97706;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: #d97706;
  cursor: pointer;
}

