.middle
{
    position: relative;
    display: inline-block;
    font-weight: 600;
}
.middle:after {    
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 0.1rem;
    left: 50%;
    position: absolute;
    background: #D97706;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}
.middle:hover:after { 
    width: 100%; 
    left: 0; 
}

